export default {
	props: {
		banners: {
			type: Array,
			default () {
				return []
			}
		}
	},
	data() {
		return {
			swiperCurrent: 0,
			swiperLength: 0,
		}
	},
	methods: {
		swiperChange(e) {
			console.log(e.detail.current)
			this.swiperCurrent = e.detail.current;
		},
	},
	computed: {
		bgColor() {
			switch (this.swiperCurrent) {
				case 0:
					return 'rgba(249,127,164,.6)';
				case 1:
					return  'rgba(224,204,197,.6)';
				case 2:
					return 'rgba(247,139,126,.6)';
				case 3:
					return 'rgba(187,96,253,.6)';
			}
		}
	}
}
